<template>
    <div class="container">
        <div class="content">

        </div>
        <div class="close" @click="this.close"></div>
    </div>
</template>

<style lang="scss" scoped>
    .container,.content{
        height: 80vh;
        width: 80vw;
        position: fixed;
        top: 10vh;
        left: 10vw;
        border-radius: 5px;
        z-index: 100;
    }
    .content{
        overflow: hidden;
        box-shadow: 0 0 10px #000;
    }
    .iframe{
        height: 100%;
        width: 100%;
    }
    .close{
        position: absolute;
        right: -15px;
        top: -15px;
        background: #fff;
        border-radius: 50%;
        box-shadow: 0 0 10px #000;
        font-size: 30px;
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 27px;
    }
</style>

<script>
export default {
    props:['data'],
    methods:{
        close(){
            this.$emit('close')
        }
    },
    watch:{
        data(newData){
            $.ajax({
                url: newData.loadUrl,
                type: 'POST',
                data: newData.para,
                success:function(data){
                    console.log(data)
                }
            })
        }
    }
}
</script>




